<script>
    export default {
        data() {
            return {
                Data:'',
                NoRules: [
                    value => (value || '').length == 4 || 'INPUTAN HARUS 4 Karakter',
                ],
                nomor:'',
                nomorUji:'',
                keyword: '',
                jumlahData: 1,
                load: false,
                headers: [{
