                total: 0,
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'tabelujiruahancpkbs/tglcam'
                this.$http.get(uri).then(response => {
                    this.ujiruahans = response.data
                    this.ujiruahans = this.ujiruahans.filter(this.Fill);
                })
            },
            Fill: function (item) {
                return item.Kesimpulan == 'Lulus';
            }, 
            tes() {
                console.log(this.Data)
            },


            printReport() {
                window.print()
            },

